const initialState2 = [{
  id: 1,
  name: '',
  parent: 0
}, {
  id: 2,
  name: '',
  parent: 0
}, {
  id: 3,
  name: '',
  parent: 1
}, {
  id: 4,
  name: '',
  parent: 1
}, {
  id: 5,
  name: '',
  parent: 2
}, {
  id: 6,
  name: '',
  parent: 5
}, {
  id: 7,
  name: '',
  parent: 6
}, {
  id: 8,
  name: '',
  parent: 2
}, {
  id: 9,
  name: '',
  parent: 5
}, {
  id: 10,
  name: '',
  parent: 8
}]

const toggleTree = (state=initialState2, action) => {
  switch(action.type) {
    case 'TOGGLE_VISIABLE':
      return state.map(item => 
        (item.id === action.id)
          ? {...item, show: !item.show || false}
          : item
      )
    case 'MOVE_NODE':
      return action.fromId === action.toId ? state : state.map(item => 
        (item.id === action.fromId)
          ? Object.assign({}, item, {parent: action.toId})
          : item
      )
    case 'SET_ACTIVE_NODE':
      return state.map(item => 
        (item.id === action.id)
          ? {...item, active: true}
          : Object.assign({}, item, {active: false})
      ) 
    default:
      return state
  }
}

export default toggleTree


